<template>
<div class="center">
  <div>
    <h3>languages</h3>
    <hr>
    <SkillBox :skills="languages" />
    <h3>tools</h3>
    <hr>
    <SkillBox :skills="tools" />
  </div>
</div>
</template>

<script>
import SkillBox from '@/components/SkillBox.vue';

export default ({
  components: {
    SkillBox
  },
  created() {
    this.$store.dispatch('randomizeLanguages');
    this.$store.dispatch('randomizeTools'); 
  },
  computed:{
    tools(){
      return this.$store.getters.tools;
    },
    languages(){
      return this.$store.getters.languages;
    }
  }
})
</script>
